<template>
  <el-row gutter="20">
    <el-col style="margin-top: 20px" :span="12"  v-for="(item,index) in web_info" :key="item['webPath']">
      <el-card>
        <template #header>
          {{item['webName']}}
        </template>
        <div>
          <el-form>
            <el-form-item label="网站虚拟路径">
              <el-input v-model="web_info[index]['webPath']"></el-input>
            </el-form-item>
            <el-form-item label="网站文件路径">
              <el-input v-model="web_info[index]['webFilePath']"></el-input>
            </el-form-item>
            <el-form-item>
              <el-button type="primary">
                更新
              </el-button>
              <el-button @click="delWeb(index)">
                删除
              </el-button>
            </el-form-item>
          </el-form>
        </div>
      </el-card>
    </el-col>
    <el-col class="add_button" :span="12" @click="addNewWebClick">
      <el-icon :size="100" class="add_button_icon"><CirclePlus /></el-icon>
    </el-col>
  </el-row><el-dialog v-model="add_web_show" title="添加站点">
  <el-form>
    <el-form-item label="网站名称">
      <el-input type="text" v-model="add_new_web_info.web_name"></el-input>
    </el-form-item>
    <el-form-item label="网站虚拟路径">
      <el-input type="text" v-model="add_new_web_info.web_path"></el-input>
    </el-form-item>
    <el-form-item label="网站文件路径">
      <el-input type="text" v-model="add_new_web_info.web_file_path"></el-input>
    </el-form-item>
  </el-form>
  <template #footer>
      <span class="dialog-footer">
        <el-button @click="add_web_show = false">Cancel</el-button>
        <el-button type="primary" @click="addNewWeb">Confirm</el-button        >
      </span>
  </template>
</el-dialog>
</template>


<script>
import {CirclePlus} from "@element-plus/icons-vue";
import axios from "axios";
import {ElMessage} from "element-plus";
export default {
  name: "sys-web-config",
  data(){
    return{
      // eslint-disable-next-line no-undef
      server:SERVER,
      web_info:[],
      add_web_show:false,
      add_new_web_info:{
        web_name:"",
        web_path:"",
        web_file_path:""
      }
    }
  },
  methods:{
    addNewWebClick:function (){
      this.add_web_show = true;
    },
    delWeb:function (index){
      axios.post(this.server + "/api/delWeb",{
        "web_file_path":this.web_info[index]['webFilePath']
      }).then((res)=>{
        ElMessage({
          message: res.data['msg'],
          type: 'success ',
        })
      }).catch((error)=>{
        ElMessage({
          message: error.response.data['msg'],
          type: 'error',
        })
      })
    },
    addNewWeb:function (){
      axios.post(this.server + "/api/addWebInfo",{
        "web_name":this.add_new_web_info.web_name,
        "web_path":this.add_new_web_info.web_path,
        "web_file_path":this.add_new_web_info.web_file_path
      }).then((res)=>{
        ElMessage({
          message: res.data['msg'],
          type: 'success ',
        })
        this.reload()
        this.add_web_show = false
      }).catch((error)=>{
        ElMessage({
          message: error.response.data['msg'],
          type: 'error',
        })
      })
    },
    getWebInfo:function (){
      axios.get(this.server + "/api/getWebInfo").then((res)=>{
        this.web_info = res.data;
      }).catch((error)=>{
        console.log(error)
      })
    },
    reload:function (){
      axios.post(this.server + "/api/reload").then((res)=>{
        ElMessage({
          message: res.data['msg'],
          type: 'success',
        })
        this.getWebInfo()
      }).catch((error)=>{
        console.log(error)
      })
    }
  },
  mounted() {
    this.getWebInfo();
  },
  components:{
    CirclePlus
  }
}
</script>

<style scoped>
  .add_button{
    margin-top: 20px;
    opacity: 1;
    transition: all 0.5s;
    border-radius: 5px;
    position: relative;
    height: 80px;
  }

  .add_button:hover{
    background-color: #409eff;
    opacity: 1;
    min-height: 220px;
  }

  .add_button_icon{
    color: #409eff;
    transition: all 0.5s;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }

  .add_button:hover .add_button_icon{
    color: white;
  }
</style>