import { createRouter,createWebHashHistory} from "vue-router";

import sysWebConfig from "@/sys-web-config";
import sysConfig from "@/sys-config";
const routes = [
    {
        path: "/",
        redirect: "/sysWebConfig",
    },
    {
        path: "/sysWebConfig",
        name: "/sysWebConfig",
        component: sysWebConfig
    },
    {
        path: "/sysConfig",
        name: "/sysConfig",
        component: sysConfig
    },

]

export const router = createRouter({
    history: createWebHashHistory(),
    routes: routes
})